import * as React from "react";
import Container from "@mui/material/Container";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import ToggleButton from "@mui/material/ToggleButton";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import Grid from "@mui/material/Unstable_Grid2";
import TextField from "@mui/material/TextField";
import { v4 as uuidv4 } from "uuid";

// ICONS
import FormatAlignLeftIcon from "@mui/icons-material/FormatAlignLeft";
import FormatAlignCenterIcon from "@mui/icons-material/FormatAlignCenter";
import FormatAlignRightIcon from "@mui/icons-material/FormatAlignRight";
import FormatAlignJustifyIcon from "@mui/icons-material/FormatAlignJustify";

// Components
import Todo from "./Todo";

// OTHERS
import { useTodos } from "../contexts/todosContext";
import { useSnak } from "../contexts/SnakContext";
import { useContext, useState, useEffect, useMemo, useReducer } from "react";
// DIALOG IMPORTS
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
//Reducer
import Reducer from "../reducers/TodoListReduce";
import { type } from "@testing-library/user-event/dist/type";
export default function TodoList() {
  const ShowSnak = useSnak();
  const [titleInput, setTitleInput] = useState("");
  const [displayedTodosType, setDisplayedTodosType] = useState("all");

    const [showDeleteDialog, setShowDeleteDialog] = useState(false);
    const [showUpdateDialog, setShowUpdateDialog] = useState(false);
    const [passed, setPassed] =useState({});
    //Reducer
    const [todos ,Dispatch ]= useTodos();
    
   
  // filteration arrays
   

  const completedTodos =useMemo(() =>{
    return todos.filter((t) => {
    console.log("completed");
    return t.isCompleted;
  });
  }, [todos]) ;

  const notCompletedTodos = useMemo(() =>{
    return todos.filter((t) => {
        console.log("not completed");

    return !t.isCompleted;
  });
  }, [todos]); 

  let todosToBeRendered = todos;

  if (displayedTodosType == "completed") {
    todosToBeRendered = completedTodos;
  } else if (displayedTodosType == "non-completed") {
    todosToBeRendered = notCompletedTodos;
  } else {
    todosToBeRendered = todos;
  }

  const todosJsx = todosToBeRendered.map((t) => {
    return <Todo key={t.id} todo={t} DelHandeler={handleDeleteClick} ModHandeler={handleUpdateClick}/>;
  });

  useEffect(() => {
    Dispatch({type:"get"});
  
}, []);

  function changeDisplayedType(e) {
    setDisplayedTodosType(e.target.value);
  }
 
  //Adding func
  function handleAddClick() {
    Dispatch({type: "Add", params: {titleInput}});
    setTitleInput("");
    ShowSnak("  ")
  }
   

  function handleDeleteClick(tod) {
    setPassed(tod);
    setShowDeleteDialog(true);
  }

  function handleDeleteDialogClose() {
    setShowDeleteDialog(false);
  }

  //Deleting function
  function handleDeleteConfirm() {
    Dispatch({type: "Delete", params: {passed}});
    setShowDeleteDialog(false);
    ShowSnak("  ")
    
  }



  function handleUpdateClick(tod) {
    setPassed(tod)
    setShowUpdateDialog(true);
  }
   

  function handleUpdateClose() {
    setShowUpdateDialog(false);
  }
  //modification func
   function handleUpdateConfirm() {
        Dispatch({type: "Update", params:{passed}})
        setShowUpdateDialog(false);
        ShowSnak("  ")
    
  }
  return ( 
    <>
    {/* DELETE DIALOG */}
          <Dialog
            style={{ direction: "rtl" }}
            onClose={handleDeleteDialogClose}
            open={showDeleteDialog}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
          >
            <DialogTitle id="alert-dialog-title">
                     
            </DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                      
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleDeleteDialogClose}></Button>
              <Button autoFocus onClick={handleDeleteConfirm}>
                  
              </Button>
            </DialogActions>
          </Dialog>
          {/* === DELETE DIALOG === */}
          {/* UPDATE DIALOG */}
      <Dialog
        style={{ direction: "rtl" }}
        onClose={handleUpdateClose}
        open={showUpdateDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title"> </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label=" "
            fullWidth
            variant="standard"
            value={passed.title}
            onChange={(e) => {
              setPassed({ ...passed, title: e.target.value });
            }}
          />

          <TextField
            autoFocus
            margin="dense"
            id="name"
            label=""
            fullWidth
            variant="standard"
            value={passed.details}
            onChange={(e) => {
              setPassed({ ...passed, details: e.target.value });
            }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleUpdateClose}></Button>
          <Button autoFocus onClick={handleUpdateConfirm}>
            
          </Button>
        </DialogActions>
      </Dialog>
      {/* === UPDATE DIALOG */}
    <Container maxWidth="sm">
      <Card sx={{ minWidth: 275 ,}}>
        <CardContent>
          <Typography variant="h2" style={{ fontWeight: "bold" }}>
            
          </Typography>
          <Divider />

          {/* FILTER BUTTONS */}
          <ToggleButtonGroup
            style={{ direction: "ltr", marginTop: "30px" }}
            value={displayedTodosType}
            exclusive
            onChange={changeDisplayedType}
            aria-label="text alignment"
          >
            <ToggleButton value="non-completed"> </ToggleButton>
            <ToggleButton value="completed"></ToggleButton>
            <ToggleButton value="all"></ToggleButton>
          </ToggleButtonGroup>
          {/* ==== FILTER BUTTON ==== */}

          {/* ALL TODOS */}
          <div style={{height:"400px", overflowY:"auto"}}>
          {todosJsx}
          </div>
          {/* === ALL TODOS === */}

          {/* INPUT + ADD BUTTON */}
          <Grid container style={{ marginTop: "20px" }} spacing={2}>
            <Grid
              xs={8}
              display="flex"
              justifyContent="space-around"
              alignItems="center"
            >
              <TextField
                style={{ width: "100%" }}
                id="outlined-basic"
                label=" "
                variant="outlined"
                value={titleInput}
                onChange={(e) => {
                  setTitleInput(e.target.value);
                }}
              />
            </Grid>

            <Grid
              xs={4}
              display="flex"
              justifyContent="space-around"
              alignItems="center"
            >
              <Button
                style={{ width: "100%", height: "100%" }}
                variant="contained"
                onClick={() => {
                  handleAddClick();
                }}
              >
                
              </Button>
            </Grid>
          </Grid>
          {/*== INPUT + ADD BUTTON ==*/}
        </CardContent>
      </Card>
    </Container>
    </>
  );
}
